.member-detail .jumbotron {
    padding: 0px;
    box-shadow: none;
}
.member-detail .member-img {
    margin-top: 20px;
    padding: 0px;
}
.member-detail .card-title {
    margin-left: 0px !important;
}
.member-detail .card-body {
    padding: 0px;
}
.member-detail .name {
    font-size: 36px;
    font-weight: bold;
    margin-top: 15px !important;
    margin-bottom: 5px !important;
    color: #0c234b;
}
.member-detail .title {
    margin-bottom: 1em;
    font-size: 18px;
    font-weight: 500;
    color: #49595e
}
.member-detail .bio {
    margin-top: 15px;
    font-size: 16px !important;
    line-height: 1.6;
    color: #403635;
}

